.login {
  height: 100vh;
  overflow: hidden;
}

.login-top {
  height: 78.5vh;
  background-image: url("../../../static/images/login-bg.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  display: flex;
}

.left {
  width: 55.9%;
}

.brandOne {
  margin-left: 9.3%;
  padding-top: 7.49%;
  display: flex;
  align-items: flex-end;
}

.brandOne img:first-child {
  width: 9.3%;
  height: 9.3%;
  margin-right: 1.5%;
}

.brandOne img:last-child {
  width: 29%;
  height: 29%;
}

.brandTwo {
  margin-left: 15.64%;
  padding-top: 7.49%;
  display: flex;
  align-items: flex-end;
}

.brandTwo img:first-child {
  width: 21.13%;
  margin-right: 2.5%;
}

.brandTwo img:last-child {
  width: 41.7%;
}

.brandThree img {
  width: 68.6%;
}

.brandThree {
  margin-left: 18.71%;
  padding-top: 7.49%;
}

.right {
  width: 45.1%;
  height: 100%;
  margin-top: 2.873%;
}

.inputBg {
  background-image: url("../../../static/images/input-bg.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  width: 72.34%;
  height: 84.5%;
  padding: 0 0.96rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcomeText {
  font-size: 24px;
  font-family: SFNSDisplay;
  color: white;
  line-height: 28px;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
  /* padding: 10.13% 0 7.23% 0; */
  padding: 70px 0 50px 0;
}

.accountInput {
  background: white;
  box-shadow: 0px 2px 2px 0px #4d79f3;
  border-radius: 30px;
  padding: 0 20px;
  width: 420px;
  height: 60px;
  display: flex;
  align-items: center;
}

.accountInput img {
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.input {
  outline: none;
  height: 100%;
  line-height: 100%;
  width: 100%;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 40px;
  padding-left: 16px;
  font-weight: 400;
  color: #999999;
  font-size: 28px;
  font: 400 28px Arial;
  display: flex;
  align-items: center;
  box-shadow: inset 0 0 0 1000px #ffffff !important;
}

.input::placeholder {
  font-size: 24px;
  font-family: SFNSDisplay;
  color: #999999;
}

.loginBtn {
  background: #7ed321;
  box-shadow: 0px 1px 1px 0px #417505;
  border-radius: 30px;
  width: 420px;
  height: 60px;
  font-size: 24px;
  font-family: SFNSDisplay;
  color: white;
  line-height: 28px;
  text-shadow: 0px 20px 40px rgba(0, 0, 0, 0.32);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
